@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Passion+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@600&display=swap);
*{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html{
    font-size: 16px;
    scroll-behavior: smooth;
}

body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-image: linear-gradient(45deg, #2f2f2f 25%, #323232 25%, #323232 50%, #2f2f2f 50%, #2f2f2f 75%, #323232 75%, #323232 100%);
    background-size: 70.71px 70.71px;
    display: flex;
    justify-content: center;
}

input{
    outline: none;
    border: none;
}

button{
    cursor: pointer;
    outline: none;
    border: none;
    background: none;
}

.app{
    position: relative;
    padding: 0 3rem;
    padding-bottom: 3rem;
    max-width: 1366px;
    width: 100%;
    min-height: 100vh;
    /* position: relative; */
}

main{
    max-width: 1440px;
    padding: 0 4rem;
    background-image: linear-gradient(136deg, #f5f5f5 20.93%, #ffffff 20.93%, #ffffff 50%, #f5f5f5 50%, #f5f5f5 70.93%, #ffffff 70.93%, #ffffff 100%);
    background-size: 61.90px 59.78px;
   
}

.main{
    padding: 4rem 0;
    padding-bottom: 1rem;
    background: white;
    min-height: 20rem;
}

@media screen and (max-width: 767px){
    .app{
        padding: 0 1rem;
    }
    main{
        padding: 0 2rem;
    }
}
@-webkit-keyframes moveUpDown{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-2rem);transform:translateY(-2rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes moveUpDown{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-2rem);transform:translateY(-2rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}.card{background:white;overflow:hidden;border-radius:1rem}.card:hover{-webkit-animation:moveUpDown .5s linear 1;animation:moveUpDown .5s linear 1}.card__id{padding-left:1rem;font-family:'Passion One', cursive;color:#bebebe}.card__img{width:100%;padding:2rem 0;background:#F2F2F2}.card__img img{display:block;margin:0 auto}.card__title{font-family:'Titillium Web', sans-serif;padding-left:1rem;font-size:1.2rem;text-transform:capitalize}.card__types{padding-top:.5rem;padding-left:1rem}

.badge{padding:.1rem 1rem;margin-right:.8rem;border-radius:.3rem;font-size:.8rem;color:white;display:inline-flex;justify-content:center;align-items:center;font-weight:600;font-family:'Exo 2', sans-serif}.badge--normal{background:#949494}.badge--fighting{background:#ff7300}.badge--flying{background:#9ba5ff}.badge--poison{background:#ae00ff}.badge--ground{background:#9c4f4f}.badge--rock{background:#475b75}.badge--bug{background:#5eff00}.badge--ghost{background:#292929}.badge--steel{background:#585151}.badge--fire{background:red}.badge--water{background:#1900ff}.badge--grass{background:green}.badge--electric{background:#fbff00}.badge--psychic{background:#ff009d}.badge--ice{background:#00c3ff}.badge--dragon{background:#c04600}.badge--dark{background:#080808}.badge--fairy{background:#ff4d91}.badge--unknown{background:#fff}.badge--shadow{background:#383232}

.dictionary{min-height:20rem}.dictionary__content{display:grid;grid-template-columns:repeat(4, 1fr);background-color:white;grid-gap:2rem 1rem;gap:2rem 1rem;padding:0 1rem}.dictionary__row{width:100%;height:4rem}@media screen and (max-width: 767px){.dictionary__content{grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 576px){.dictionary__content{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 400px){.dictionary__content{grid-template-columns:repeat(1, 1fr)}}

@-webkit-keyframes loading{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading{0%{-webkit-transform:rotate(0);transform:rotate(0)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading{display:flex;justify-content:center;align-items:center}.loading--small{padding-top:1rem}.loading--small .pokeball{width:3rem;height:3rem}.loading--small .pokeball__middle{width:1rem;height:1rem;border:2px solid black}.loading--big{background-image:linear-gradient(45deg, #2f2f2f 25%, #323232 25%, #323232 50%, #2f2f2f 50%, #2f2f2f 75%, #323232 75%, #323232 100%);background-size:70.71px 70.71px;position:absolute;top:0;left:0;width:100%;height:100%;z-index:10}.loading--big .pokeball{width:6rem;height:6rem}.loading--big .pokeball__middle{width:2rem;height:2rem;border:4px solid black}.loading .pokeball{border-radius:100%;overflow:hidden;position:relative;-webkit-animation:loading 1.5s linear infinite;animation:loading 1.5s linear infinite}.loading .pokeball__top{background-color:red;height:50%;z-index:1}.loading .pokeball__bottom{background-color:white;height:50%;z-index:1}.loading .pokeball__line{position:absolute;width:100%;height:10%;background-color:black;z-index:2;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.loading .pokeball__middle{position:absolute;z-index:3;background-color:white;border-radius:100%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.header{position:relative;padding:3rem}.header img{display:block;margin:0 auto;width:35%;height:auto;cursor:pointer}@media screen and (max-width: 576px){.header img{width:80%}}@media screen and (max-width: 320px){.header img{width:100%}}

.search{position:relative;display:flex;justify-content:center;width:100%;padding:1rem 0;background-color:#616161;position:relative;z-index:1}.search::after,.search::before{content:"";position:absolute;width:10rem;height:5rem;z-index:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);background-color:#616161}.search::after{border-bottom-left-radius:8rem;border-bottom-right-radius:8rem;top:60%}.search::before{border-top-left-radius:8rem;border-top-right-radius:8rem;top:-60%}.search form{z-index:1;position:relative;width:80%}.search__input{width:100%;height:3.5rem;padding-left:2rem;border:1px solid black;font-size:1.2rem;border-radius:2rem;padding-right:4rem}.search__button{position:absolute;top:0;right:0;width:3.5rem;height:100%;border-radius:50%;font-size:1.2rem;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 767px){.search::after,.search::before{width:8rem;height:4rem}.search::before{border-top-left-radius:8rem;border-top-right-radius:8rem;bottom:60%;top:auto}.search__input{height:3rem;padding-left:1.5rem;border:1px solid black;font-size:1rem;border-radius:2rem;padding-right:3rem}}@media screen and (max-width: 576px){.search__input{height:2.5rem;padding-left:1rem;font-size:.8rem;padding-right:3rem}.search::after,.search::before{width:7rem;height:3.5rem}.search__button{width:3rem;font-size:1rem}}

.err__content{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:row}.err__img{min-width:10rem;max-width:10rem;height:10rem}.err__img img{display:block;width:100%;height:auto}.err__text{font-size:1.3rem;font-family:'Chakra Petch', sans-serif}

@-webkit-keyframes fadeInBtn{0%{opacity:0}100%{opacity:1}}@keyframes fadeInBtn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes moveUpDownBtn{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-1rem);transform:translateY(-1rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes moveUpDownBtn{0%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-1rem);transform:translateY(-1rem)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}.scroll-top{position:fixed;bottom:2rem;right:2rem;background-image:linear-gradient(136deg, #f5f5f5 20.93%, #fff 20.93%, #fff 50%, #f5f5f5 50%, #f5f5f5 70.93%, #fff 70.93%, #fff 100%);background-size:61.90px 59.78px;width:3rem;height:3rem;display:block;z-index:100;border-radius:50%;font-size:2rem;-webkit-animation:fadeInBtn .2s ease-in;animation:fadeInBtn .2s ease-in}.scroll-top:hover{-webkit-animation:moveUpDownBtn 1s linear infinite;animation:moveUpDownBtn 1s linear infinite}

